import Vue from 'vue';
import VueRouter from 'vue-router';

import HomeView from '@/views/Home/HomeView.vue';
import RecommendView from '@/views/Home/childPages/RecommendView.vue';
import HotMusicView from '@/views/Home/childPages/HotMusicView.vue';
import SearchView from '@/views/Home/childPages/SearchView.vue';

import songListView from '@/views/songListView.vue';

import ArtistView from '@/views/ArtistView.vue';
import PingLunView from '@/views/PingLunView.vue';

import PlaySongView from '@/views/PlaySong/PlaySongView.vue';
import SongView from '@/views/PlaySong/childPages/SongView.vue';
import LyricView from '@/views/PlaySong/childPages/LyricView.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    components: {
      appPage: HomeView
    },
    children: [
      {
        path: '/',
        components: { tagPage: RecommendView }
      },
      {
        path: '/hot',
        components: { tagPage: HotMusicView }
      },
      {
        path: '/search',
        components: { tagPage: SearchView }
      }
    ]
  },
  {
    path: '/songList',
    components: {
      appPage: songListView
    }
  },
  {
    path: '/artist',
    components: {
      appPage: ArtistView
    }
  },
  {
    path: '/comments',
    components: { appPage: PingLunView }
  },
  {
    path: '/palyPage',
    components: { appPage: PlaySongView },
    children: [
      {
        path: '/palyPage',
        components: {
          songAndLyric: SongView
        }
      },
      {
        path: '/palyPage/lyric',
        components: {
          songAndLyric: LyricView
        }
      }
    ]
  }
];

const router = new VueRouter({
  routes
});

export default router;