<script>
import lodash from 'lodash';
export default {
    name: 'PlaySongView',
    computed: {
        playingMusicObj: {
            get: function () {
                return this.$store.state.playingMusicObj;
            },
            set(value) {
                this.$store.commit({ type: 'setPlayingMusicObj', value: value });
                localStorage.setItem('playingMusicObj', JSON.stringify(value));
            }
        },
        playingSong: {
            get: function () {
                return this.playingMusicObj.playingSong;
            },
            set: function (value) {
                let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
                playingMusicObj.playingSong = value;
                this.playingMusicObj = playingMusicObj;
                localStorage.setItem('playingMusicObj', JSON.stringify(this.playingMusicObj));
            }
        },
        nowTime: function () {
            let playing = this.playingMusicObj.playing;
            let mm = parseInt(playing / 60) < 10 ? '0' + parseInt(playing / 60) : parseInt(playing / 60);
            let ss = parseInt(playing % 60) < 10 ? '0' + parseInt(playing % 60) : parseInt(playing % 60);
            return mm + ':' + ss;
        },
        totalTime: function () {
            let totalTime = this.playingSong.totalTime;
            let mm = parseInt(totalTime / 60) < 10 ? '0' + parseInt(totalTime / 60) : parseInt(totalTime / 60);
            let ss = parseInt(totalTime % 60) < 10 ? '0' + parseInt(totalTime % 60) : parseInt(totalTime % 60);
            return mm + ':' + ss;
        },
        modeIndex: {
            get: function () {
                return this.$store.state.modeIndex;
            },
            set: function (value) {
                this.$store.commit({ type: 'setModeIndex', value: value });
            }
        },
        playingSongList: {
            get: function () {
                return this.$store.state.playingSongList;
            },
            set: function (value) {
                this.$store.commit({ type: 'setPlayingSongList', value: value });
                localStorage.setItem('playingSongList', JSON.stringify(value));
            }
        },
        isShowPlayingList: {
            get: function () {
                return this.$store.state.isShowPlayingList;
            },
            set: function (value) {
                this.$store.commit({ type: 'setIsShowPlayingList', value: value });
            }
        },
        getBackgroundUrl: {
            get: function () {
                let playingSong = this.playingSong;
                return `url(${playingSong.coverURL})`;
            }
        }
    },
    methods: {
        changeMethod: function (newValue) {
            let songPlayer = document.querySelector('#app>.musicAudio');
            songPlayer.currentTime = newValue;
        },
        spanClick: function () {
            if (this.modeIndex < 3) {
                ++this.modeIndex;
            } else {
                this.modeIndex = 1;
            }
        },
        prevIconClick: function (playingSong) {
            let nowSongIndex;
            let playingSongList = this.playingSongList;
            playingSongList.forEach((v, i) => {
                if (playingSong.id == v.id) {
                    nowSongIndex = i;
                }
            });
            --nowSongIndex;
            let previndex;
            if (nowSongIndex >= 0 && nowSongIndex <= playingSongList.length - 1) {
                previndex = nowSongIndex;
            } else {
                previndex = playingSongList.length - 1;
            }
            let prevSong = lodash.cloneDeep(playingSongList[previndex]);
            this.playingSong = prevSong;

            let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
            playingMusicObj.isPlay = true;
            this.playingMusicObj = playingMusicObj;

            let audio = document.querySelector('#app>audio');
            setTimeout(() => {
                audio.play();
            }, 1000);
        },
        backconClick: function (playingSong) {
            let nowSongIndex;
            let playingSongList = this.playingSongList;
            playingSongList.forEach((v, i) => {
                if (playingSong.id == v.id) {
                    nowSongIndex = i;
                }
            });

            let backIndex;
            ++nowSongIndex;
            if (nowSongIndex >= 0 && nowSongIndex <= playingSongList.length - 1) {
                backIndex = nowSongIndex;
            } else {
                backIndex = 0;
            }

            let backSong = lodash.cloneDeep(playingSongList[backIndex]);
            this.playingSong = backSong;

            let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
            playingMusicObj.isPlay = true;
            this.playingMusicObj = playingMusicObj;

            let audio = document.querySelector('#app>audio');
            setTimeout(() => {
                audio.play();
            }, 1000);
        },
        playListiconClick: function () {
            this.isShowPlayingList = true;
        },
        isPlayClick: function () {
            let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
            playingMusicObj.isPlay = !playingMusicObj.isPlay;
            this.playingMusicObj = playingMusicObj;

            let audio = document.querySelector('#app>.musicAudio');
            let playing = playingMusicObj.playing;
            if (this.playingMusicObj.isPlay) {
                audio.currentTime = playing;
                audio.play();
            } else {
                audio.pause();
            }
        },
        backIconClick: function () {
            this.$router.go(-1);
        },
        pinglunClick: function () {
            this.$router.push({ path: '/comments', query: { commentType: 'music', id: this.playingSong.id } });
        }
    }
};
</script>

<style lang="less">
.PlaySongView {
    height: 100vh;
    position: relative;
    overflow: hidden;

    >.cover {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        z-index: -1;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        filter: brightness(20%);
    }

    >header {
        height: 10vh;
        display: flex;
        justify-content: space-around;
        align-items: center;

        >.icon {
            width: 35rem;
            height: 35rem;
        }

        >.title {
