<template>
    <div class="SearchResult">
        <section class="bastMul" v-show="!loading">
            <p></p>
            <!--  -->
            <!-- https://apis.netstart.cn/music/artist/detail?id=6452  -->
            <!-- https://apis.netstart.cn/music/artists?id=6452  -->
            <div class="artist" v-for="(item) in artist" @click="artistClick(item.id)" :key="item.id">
                <div class="left">
                    <!-- artist[0].img1v1Url -->
                    <img :src="item.img1v1Url">
                    <span class="songer">{{ item.occupation }}:{{ item.name }}{{ item.alias ? '(' + item.alias + ")" :
                            ""
                    }}</span>
                </div>
                <div class="right">
                    <svg t="1665150232011" class="enterIcon" viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="3922" width="128" height="128">
                        <path
                            d="M970.496 543.829333l30.165333-30.165333-415.829333-415.914667a42.837333 42.837333 0 0 0-60.288 0 42.538667 42.538667 0 0 0 0 60.330667l355.413333 355.498667-355.413333 355.285333a42.496 42.496 0 0 0 0 60.288c16.64 16.64 43.861333 16.469333 60.288 0.042667l383.914667-383.701334 1.749333-1.664z"
                            fill="#2c2c2c" p-id="3923"></path>
                    </svg>
                </div>
            </div>

            <!--  -->
            <div class="playlist" v-for="(item) in playlist" @click="playlistClick(item.id)" :key="item.id">
                <div class="left">
                    <!-- playlist[0].coverImgUrl -->
                    <img :src="item.coverImgUrl">
                    <div class="msg">
                        <p>: {{ item.name }}</p>
                        <p>by {{ item.nickname }}</p>
                    </div>
                </div>
                <div class="right">
                    <svg t="1665150232011" class="enterIcon" viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="3922" width="128" height="128">
                        <path
                            d="M970.496 543.829333l30.165333-30.165333-415.829333-415.914667a42.837333 42.837333 0 0 0-60.288 0 42.538667 42.538667 0 0 0 0 60.330667l355.413333 355.498667-355.413333 355.285333a42.496 42.496 0 0 0 0 60.288c16.64 16.64 43.861333 16.469333 60.288 0.042667l383.914667-383.701334 1.749333-1.664z"
                            fill="#2c2c2c" p-id="3923"></path>
                    </svg>
                </div>
            </div>
        </section>

        <van-list class="songsList" v-model="loading" :finished="finished" finished-text="" @load="onLoad">
            <SongItemComp v-for="(item, index) in songList" :key="index" :songItem="item" :index="index"
                :songList="songList"></SongItemComp>
        </van-list>
    </div>
</template>

<script>
import axios from 'axios';
import SongItemComp from './SongItemComp.vue';
export default {
    name: 'SearchResult',
    props: {
        inputWord: String
    },
    data: function () {
        return {
            songList: [],
            // 
            // 
            artist: [],
            // 
            playlist: [],
            offset: 0,
            // van-list
            loading: false,
            finished: false,
        }
    },
    components: {
        SongItemComp
    },
    created: function () {
        let _this = this;
        // https://apis.netstart.cn/music/search/multimatch?keywords=
        axios.get('https://apis.netstart.cn/music/cloudsearch', { params: { keywords: this.inputWord, limit: 30, offset: this.offset * 30 } })
            .then((data) => {
                data.data.result.songs.forEach((v) => {
                    let ars = v.ar;
                    let prevName = [];
                    ars.forEach((v) => {
                        prevName.push(v.name);
                    });
                    prevName = prevName.join('/');

                    _this.songList.push({
                        id: v.id,
                        // URL
                        URL: `https://music.163.com/song/media/outer/url?id=${v.id}.mp3`,
                        // (xxx)
                        totalTime: parseInt(v.dt / 1000),
                        // 
                        coverURL: v.al.picUrl,
                        // 
                        songName: v.name,
                        // 
                        prevName: prevName,
                        // 
                        backName: v.al.name,
                        // 
                        sqMusic: v.sq
                    });
                });

                return axios.get('https://apis.netstart.cn/music/search/multimatch', { params: { keywords: this.inputWord } });
            })
            .then((data) => {
                let result = data.data.result;
                let _this = this;
                if (result) {
                    if (result.orders.includes('artist')) {
                        result.artist.forEach((v) => {
                            let alias = undefined;
                            let al = [];
                            v.alias.forEach((v) => {
                                al.push(v);
                            });
                            alias = al.join('/');
                            _this.artist.push({
                                img1v1Url: v.img1v1Url,
                                occupation: v.occupation,
                                name: v.name,
                                alias: alias,
                                id: v.id
                            });
                        });
                    }
                    if (result.orders.includes('playlist')) {
                        result.playlist.forEach((v) => {
                            _this.playlist.push({
                                coverImgUrl: v.coverImgUrl,
                                name: v.name,
                                nickname: v.creator.nickname,
                                id: v.id
                            });
                        });
                    }
                }
            });
    },
    methods: {
        playlistClick: function (id) {
            this.$router.push({
                path: '/songList', query: {
                    id: id
                }
            });
        },
        artistClick: function (id) {
            this.$router.push({
                path: '/artist', query: {
                    id: id
                }
            });
        },
        onLoad() {
            // 
            // setTimeout  ajax 
            let _this = this;
            ++this.offset;
            axios.get('https://apis.netstart.cn/music/cloudsearch', { params: { keywords: this.inputWord, limit: 30, offset: this.offset * 30 } })
                .then((data) => {
                    let songs = data.data.result.songs;
                    songs.forEach((v) => {
                        let ars = v.ar;
                        let prevName = [];
                        ars.forEach((v) => {
                            prevName.push(v.name);
                        });
                        prevName = prevName.join('/');

                        _this.songList.push({
                            id: v.id,
                            // URL
                            URL: `https://music.163.com/song/media/outer/url?id=${v.id}.mp3`,
                            // (xxx)
                            totalTime: parseInt(v.dt / 1000),
                            // 
                            coverURL: v.al.picUrl,
                            // 
                            songName: v.name,
                            // 
                            prevName: prevName,
                            // 
                            backName: v.al.name,
                            // 
                            sqMusic: v.sq
                        });

                        // 
                        this.loading = false;

                        // 
                        if (songs.length == 0) {
                            this.finished = true;
                        }
                    });
                });
        }
    }
}
</script>

<style lang="less">
.SearchResult {

    >.loading {
        padding: 50rem 0;

        >img {
            display: block;
            width: 50rem;
            margin: 0 auto;
        }
    }

    >.bastMul {
        >p {
            font-size: 20rem;
            padding: 10rem;
        }

        >.artist,
        >.playlist {
            >.left {
                >img {
                    width: 50rem;
                    margin-right: 5rem;
                }
            }

            >.right {
                display: flex;
                align-items: center;

                .enterIcon {
                    width: 15rem;
                    height: $width;
                }
            }
        }

        >.artist {
            display: flex;
            justify-content: space-between;
            padding: 10rem;

            >.left {
                display: flex;
                align-items: center;

                >.songer {
                    display: block;
                    width: 264rem;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    font-size: 14rem;
                }
            }
        }

        >.playlist {
            display: flex;
            justify-content: space-between;
            padding: 10rem;

            >.left {
                display: flex;

                >.msg {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;

                    p:nth-child(1) {
                        font-size: 14rem;
                        margin-bottom: 5rem;
                    }

                    p:nth-child(2) {
                        font-size: 14rem;
                        color: gray;
                    }
                }
            }
        }
    }
}
</style>