<template>
    <div class="PingLunView">
        <header>
            <div class="content">
                <svg @click='$router.go(-1);' t="1667463614433" class="icon" viewBox="0 0 1024 1024" version="1.1"
                    xmlns="http://www.w3.org/2000/svg" p-id="2308" width="128" height="128">
                    <path
                        d="M393.390114 512.023536l347.948667-336.348468c20.50808-19.85828 20.50808-51.997258 0-71.792093-20.507056-19.826558-53.778834-19.826558-74.28589 0L281.990954 476.135164c-20.476357 19.826558-20.476357 51.981908 0 71.746044l385.061936 372.236839c10.285251 9.91379 23.728424 14.869662 37.173644 14.869662 13.446243 0 26.889417-4.956895 37.112246-14.901385 20.50808-19.826558 20.50808-51.919487 0-71.746044L393.390114 512.023536"
                        p-id="2309" fill="#2c2c2c"></path>
                </svg>
                <span></span>
                <span class="commentNum">({{ commentTotal }})</span>
            </div>
        </header>

        <section class="comment">
            <header>
                <div class="content">
                    <div class="left"></div>
                    <div class="right">
                        <span v-for="(item, index) in allType" :key="index" :class="{ 'active': nowType == item.type }"
                            @click="nowType = item.type">{{ item.content }}</span>
                    </div>
                </div>
            </header>
            <div class="content">
                <transition-group name="list">
                    <div v-for="(item, index) in allType" :key="index" v-show="nowType == item.type">
                        <van-list v-model="loading" :finished="item.finished" finished-text="" @load="onLoad">
                            <commentItemComponent v-for="(comment, i) in item.comments" :pingLunItem="comment" :key="i">
                            </commentItemComponent>
                        </van-list>
                    </div>
                </transition-group>
            </div>
        </section>
    </div>
</template>

<script>
import axios from 'axios';
import lodash from 'lodash';
import commentItemComponent from '@/components/commentItemComponent.vue';
export default {
    name: 'PingLunView',
    data: function () {
        return {
            allType: [
                { type: 'tui', content: '', comments: [], finished: true },
                { type: 'hot', content: '', comments: [], finished: true },
                { type: 'new', content: '', comments: [], finished: false }
            ],
            // 
            nowType: 'tui',
            // 
            commentTotal: 0,
            offset: 0,
            // list
            loading: false,
        };
    },
    components: {
        commentItemComponent
    },
    methods: {
        onLoad() {
            if (this.nowType == 'new') {
                ++this.offset;
                // 
                axios.get(`https://apis.netstart.cn/music/comment/${this.$route.query.commentType}`, { params: { id: this.$route.query.id, limit: 20, offset: this.offset * 20 } })
                    .then((data) => {
                        let total = data.data.total;
                        let comments = data.data.comments;
                        this.commentTotal = total;

                        comments.forEach((v) => {
                            let item = {
                                // 
                                avatarUrl: v.user.avatarUrl,
                                // id
                                avatarId: v.user.userId,
                                // 
                                nickname: v.user.nickname,
                                // vip
                                vipRights: v.user.vipRights,
                                // id
                                commentId: v.commentId,
                                // 
                                content: v.content,
                                // 
                                time: v.time,
                                // 
                                likedCount: v.likedCount
                            };
                            this.allType[2].comments.push(item);
                        });

                        // 
                        this.loading = false;

                        // 
                        if (comments.length == 0) {
                            this.allType[2].finished = true;
                        }
                    });
            }
        }
    },
    computed: {
        playingMusicObj: {
            get: function () {
                return this.$store.state.playingMusicObj;
            },
            set(value) {
                this.$store.commit({ type: 'setPlayingMusicObj', value: value });
                localStorage.setItem('playingMusicObj', JSON.stringify(value));
            }
        },
        playingSong: {
            get: function () {
                return this.playingMusicObj.playingSong;
            },
            set: function (value) {
                let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
                playingMusicObj.playingSong = value;
                this.playingMusicObj = playingMusicObj;
                localStorage.setItem('playingMusicObj', JSON.stringify(this.playingMusicObj));
            }
        },
    },
    created: function () {
        axios.get(`https://apis.netstart.cn/music/comment/${this.$route.query.commentType}`, { params: { id: this.$route.query.id, limit: 20, offset: this.offset * 20 } })
            .then((data) => {
                let total = data.data.total;
                let comments = data.data.comments;
                let hotComments = data.data.hotComments;
                this.commentTotal = total;

                comments.forEach((v) => {
                    let item = {
                        // 
                        avatarUrl: v.user.avatarUrl,
                        // id
                        avatarId: v.user.userId,
                        // 
                        nickname: v.user.nickname,
                        // vip
                        vipRights: v.user.vipRights,
                        // id
                        commentId: v.commentId,
                        // 
                        content: v.content,
                        // 
                        time: v.time,
                        // 
                        likedCount: v.likedCount
                    };
                    this.allType[2].comments.push(item);
                });
                hotComments.forEach((v) => {
                    let item = {
                        // 
                        avatarUrl: v.user.avatarUrl,
                        // id
                        avatarId: v.user.userId,
                        // 
                        nickname: v.user.nickname,
                        // vip
                        vipRights: v.user.vipRights,
                        // id
                        commentId: v.commentId,
                        // 
                        content: v.content,
                        // 
                        time: v.time,
                        // 
                        likedCount: v.likedCount
                    };
                    this.allType[0].comments.push(item);
                    this.allType[1].comments.push(item);
                });
            });
    },
    watch: {
        'playingSong.id': {
            handler: function (newValue) {
                if (this.$route.query.commentType == 'music') {
                    this.offset = 0;
                    this.commentTotal = 0;
                    this.allType = [
                        { type: 'tui', content: '', comments: [], finished: true },
                        { type: 'hot', content: '', comments: [], finished: true },
                        { type: 'new', content: '', comments: [], finished: false }
                    ];
                    this.nowType = 'tui';
                    axios.get(`https://apis.netstart.cn/music/comment/${this.$route.query.commentType}`, { params: { id: newValue, limit: 20, offset: this.offset * 20 } })
                        .then((data) => {
                            let total = data.data.total;
                            let comments = data.data.comments;
                            let hotComments = data.data.hotComments;
                            this.commentTotal = total;

                            comments.forEach((v) => {
                                let item = {
                                    // 
                                    avatarUrl: v.user.avatarUrl,
                                    // id
                                    avatarId: v.user.userId,
                                    // 
                                    nickname: v.user.nickname,
                                    // vip
                                    vipRights: v.user.vipRights,
                                    // id
                                    commentId: v.commentId,
                                    // 
                                    content: v.content,
                                    // 
                                    time: v.time,
                                    // 
                                    likedCount: v.likedCount
                                };
                                this.allType[2].comments.push(item);
                            });
                            hotComments.forEach((v) => {
                                let item = {
                                    // 
                                    avatarUrl: v.user.avatarUrl,
                                    // id
                                    avatarId: v.user.userId,
                                    // 
                                    nickname: v.user.nickname,
                                    // vip
                                    vipRights: v.user.vipRights,
                                    // id
                                    commentId: v.commentId,
                                    // 
                                    content: v.content,
                                    // 
                                    time: v.time,
                                    // 
                                    likedCount: v.likedCount
                                };
                                this.allType[0].comments.push(item);
                                this.allType[1].comments.push(item);
                            });
                        });
                }
            }
        }
    }
};
</script>

<style lang="less">
.PingLunView {
    background-color: rgb(241, 243, 244);

    >header {
        background-color: #fff;
        padding: 15rem 0;

        >.content {
            width: 95%;
            margin: 0 auto;
            display: flex;
            align-items: center;

            >.icon {
                width: 25rem;
                height: 25rem;
                margin-right: 10rem;
            }

            >span {
                font-size: 20rem;
                font-weight: bold;
            }
        }
    }

    >.comment {
        background-color: #fff;
        margin: 10rem 0 0 0;

        >header {
            padding: 10rem 0;

            >.content {
                width: 95%;
                margin: 10rem auto;
                display: flex;
                align-items: center;
                justify-content: space-between;

                >.left {
                    font-size: 16rem;
                }

                >.right {
                    font-size: 14rem;

                    >span {
                        margin: 0 5rem;
                        color: gray;
                    }

                    .active {
                        color: black;
                    }
                }

                >.left,
                >.right {
                    font-weight: bold;
                }
            }
        }
    }

    .list-enter,
    .list-leave-to {
        opacity: 0;
        transform: translateX(100vw);
    }

    .list-enter-active,
    .list-leave-active {
        transition: all 0.5s linear;
    }

    .list-enter-to,
    .list-leave {
        opacity: 1;
        transform: translateX(0);
    }
}
</style>