<script>
import lodash from 'lodash';
import PlayingListItemComponent from '@/components/PlayingListItemComponent.vue';
export default {
  name: 'App',
  computed: {
    playingMusicObj: {
      get: function () {
        return this.$store.state.playingMusicObj;
      },
      set(value) {
        this.$store.commit({ type: 'setPlayingMusicObj', value: value });
        localStorage.setItem('playingMusicObj', JSON.stringify(value));
      }
    },
    isShowPlayingList: {
      get: function () {
        return this.$store.state.isShowPlayingList;
      },
      set: function (value) {
        this.$store.commit({ type: 'setIsShowPlayingList', value: value });
      }
    },
    playingSong: {
      get: function () {
        return this.playingMusicObj.playingSong;
      },
      set: function (value) {
        let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
        playingMusicObj.playingSong = value;
        this.playingMusicObj = playingMusicObj;
        localStorage.setItem('playingMusicObj', JSON.stringify(this.playingMusicObj));
      }
    },
    modeIndex: {
      get: function () {
        return this.$store.state.modeIndex;
      },
      set: function (value) {
        this.$store.commit({ type: 'setModeIndex', value: value });
      }
    },
    currentRate: {
      get: function () {
        let playingSong = this.playingSong;
        let playingMusicObj = this.playingMusicObj;
        return playingMusicObj.playing / playingSong.totalTime * 100;
      }
    },
    titleContent: function () {
      let content;
      switch (true) {
        case this.modeIndex == 1: content = ''; break;
        case this.modeIndex == 2: content = ''; break;
        case this.modeIndex == 3: content = ''; break;
      }
      return content;
    },
    playingSongList: {
      get: function () {
        return this.$store.state.playingSongList;
      },
      set: function (value) {
        this.$store.commit({ type: 'setPlayingSongList', value: value });
        localStorage.setItem('playingSongList', JSON.stringify(value));
      }
    },
    isShowContr: {
      get: function () {
        let isInCludes = this.$route.path.includes('palyPage');
        return this.playingSong.id && !isInCludes ? true : false;
      }
    }
  },
  methods: {
    audioTimeupdate: function () {
      let audio = document.querySelector('#app>.musicAudio');

      let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
      playingMusicObj.playing = audio.currentTime;
      this.playingMusicObj = playingMusicObj;
    },
    modeCtrl: function () {
      let audio = document.querySelector('#app>.musicAudio');
      if (this.modeIndex == 1) {
        setTimeout(() => {
          audio.play();
        }, 500);
      }
      if (this.modeIndex == 2 || this.modeIndex == 3) {
        this.backconClick(this.playingSong);
      }
    },
    songNameClick: function () {
      this.$router.push({ path: '/palyPage' });
    },
    isPlayingClick: function () {
      let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
      playingMusicObj.isPlay = !playingMusicObj.isPlay;
      this.playingMusicObj = playingMusicObj;

      let audio = document.querySelector('#app>.musicAudio');
      let playing = playingMusicObj.playing;
      if (this.playingMusicObj.isPlay) {
        audio.currentTime = playing;
        audio.play();
      } else {
        audio.pause();
      }
    },
    playingListClick: function () {
      this.isShowPlayingList = false;
    },
    spanClick: function () {
      if (this.modeIndex < 3) {
        ++this.modeIndex;
      } else {
        this.modeIndex = 1;
      }
    },
    backconClick: function (playingSong) {
      let nowSongIndex;
      let playingSongList = this.playingSongList;
      playingSongList.forEach((v, i) => {
        if (playingSong.id == v.id) {
          nowSongIndex = i;
        }
      });

      let backIndex;
      ++nowSongIndex;
      if (nowSongIndex >= 0 && nowSongIndex <= playingSongList.length - 1) {
        backIndex = nowSongIndex;
      } else {
        backIndex = 0;
      }

      let backSong = lodash.cloneDeep(playingSongList[backIndex]);
      this.playingSong = backSong;

      let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
      playingMusicObj.isPlay = true;
      this.playingMusicObj = playingMusicObj;

      let audio = document.querySelector('#app>audio');
      setTimeout(() => {
        audio.play();
      }, 1000);
    }
  },
  watch: {
    modeIndex: {
      handler: function (newValue) {
        if (newValue == 3) {
          let playingSongList = lodash.cloneDeep(this.playingSongList);
          playingSongList.sort(() => {
            return Math.random() - Math.random();
          });
          this.playingSongList = playingSongList;
        }
      }
    }
  },
  components: {
    PlayingListItemComponent
  },
  created: function () {
    let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
    playingMusicObj.isPlay = false;
    this.playingMusicObj = playingMusicObj;
  }
}
</script>

