<template>
    <div class="commentItemComponent">
        <div class="content">
            <header>
                <div class="left">
                    <img class="avatarUrl" :src="pingLunItem.avatarUrl">
                    <div class="more">
                        <div class="top">
                            <span class="name">{{ pingLunItem.nickname }}</span>
                            <img v-if="pingLunItem.vipRights" :src="iconUrl" class="icon">
                        </div>
                        <div class="buttom">
                            <span>{{ time }}</span>
                        </div>
                    </div>
                </div>
                <div class="right">
                    <span :class="{ active: isLike }">{{ likedCount }}</span>
                    <div class="icons" @click="iconsClick()">
                        <svg t="1667472607423" v-if="!isLike" class="icon" viewBox="0 0 1024 1024" version="1.1"
                            xmlns="http://www.w3.org/2000/svg" p-id="3454" width="128" height="128">
                            <path
                                d="M621.674667 408.021333c16.618667-74.24 28.224-127.936 34.837333-161.194666C673.152 163.093333 629.941333 85.333333 544.298667 85.333333c-77.226667 0-116.010667 38.378667-138.88 115.093334l-0.586667 2.24c-13.728 62.058667-34.72 110.165333-62.506667 144.586666a158.261333 158.261333 0 0 1-119.733333 58.965334l-21.909333 0.469333C148.437333 407.808 106.666667 450.816 106.666667 503.498667V821.333333c0 64.8 52.106667 117.333333 116.394666 117.333334h412.522667c84.736 0 160.373333-53.568 189.12-133.92l85.696-239.584c21.802667-60.96-9.536-128.202667-70.005333-150.186667a115.552 115.552 0 0 0-39.488-6.954667H621.674667z"
                                p-id="3455" fill="#8a8a8a"></path>
                        </svg>
                        <svg t="1667472607423" v-else class="icon" viewBox="0 0 1024 1024" version="1.1"
                            xmlns="http://www.w3.org/2000/svg" p-id="3454" width="128" height="128">
                            <path
                                d="M621.674667 408.021333c16.618667-74.24 28.224-127.936 34.837333-161.194666C673.152 163.093333 629.941333 85.333333 544.298667 85.333333c-77.226667 0-116.010667 38.378667-138.88 115.093334l-0.586667 2.24c-13.728 62.058667-34.72 110.165333-62.506667 144.586666a158.261333 158.261333 0 0 1-119.733333 58.965334l-21.909333 0.469333C148.437333 407.808 106.666667 450.816 106.666667 503.498667V821.333333c0 64.8 52.106667 117.333333 116.394666 117.333334h412.522667c84.736 0 160.373333-53.568 189.12-133.92l85.696-239.584c21.802667-60.96-9.536-128.202667-70.005333-150.186667a115.552 115.552 0 0 0-39.488-6.954667H621.674667z"
                                p-id="3455" fill="#d81e06"></path>
                        </svg>
                    </div>
                </div>
            </header>
            <section class="content">
                {{ pingLunItem.content }}
            </section>
        </div>
    </div>
</template>

<script>
import moment from 'moment';
import lodash from 'lodash';
export default {
    name: 'commentItemComponent',
    props: { pingLunItem: Object },
    computed: {
        iconUrl: {
            get: function () {
                let vipRights = this.pingLunItem.vipRights;
                let associator = vipRights.associator;
                if (vipRights && associator) {
                    return associator.iconUrl;
                } else {
                    return '';
                }
            }
        },
        time: {
            get: function () {
                let date = new Date(this.pingLunItem.time);
                return moment(date).format('YYYY-MM-DD');
            }
        },
        likeComments: {
            get: function () {
                return this.$store.state.likeComments;
            },
            set: function (value) {
                this.$store.commit({ type: 'setLikeComments', value: value });
            }
        },
        isLike: {
            get: function () {
                let commentId = this.pingLunItem.commentId;
                let bool = this.likeComments.includes(commentId);
                return bool;
            }
        },
        likedCount: {
            get: function () {
                let likedCount = this.pingLunItem.likedCount;
                return this.isLike ? likedCount + 1 : likedCount;
            }
        }
    },
    methods: {
        iconsClick: function () {
            let commentId = this.pingLunItem.commentId;
            let likeComments = lodash.cloneDeep(this.likeComments);
            let bool = likeComments.includes(commentId);
            if (bool) {
                likeComments.forEach((v, i) => {
                    if (v == commentId) {
                        likeComments.splice(i, 1);
                    }
                });
            } else {
                likeComments.push(commentId);
            }
            this.likeComments = likeComments;
            localStorage.setItem('likeComments', JSON.stringify(likeComments));
        }
    }
};
</script>

<style lang="less">
.commentItemComponent {
    border-bottom: 1px solid #f1f3f4;
    padding: 10rem 0;

    >.content {
        width: 95%;
        margin: 0 auto;

        >header {
            display: flex;
            justify-content: space-between;

            >.left {
                display: flex;
                align-items: center;

                .avatarUrl {
                    display: block;
                    width: 40rem;
                    height: $width;
                    border-radius: 50%;
                }

                >.more {
                    margin: 0 0 0 10rem;

                    >.top {
                        display: flex;
                        align-items: center;
                        margin-bottom: 5rem;

                        .name {
                            font-size: 16rem;
                            font-weight: bold;
                            max-width: 150rem;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }

                        .icon {
                            display: block;
                            width: 45rem;
                            margin: 0 5rem;
                        }
                    }

                    >.buttom {
                        span {
                            font-size: 12rem;
                            color: gray;
                        }
                    }
                }
            }

            >.right {
                display: flex;
                align-items: center;

                >span {
                    font-size: 12rem;
                    color: gray;
                }

                >.icons {
                    .icon {
                        width: 20rem;
                        height: 20rem;
                        margin: 0 5rem;
                    }
                }

                >.active {
                    color: red;
                }
            }
        }

        >.content {
            padding: 5rem 0;
            font-size: 14rem;
            width: 95%;
            margin: 0 auto;
        }
    }
}
</style>