<template>
    <div class="HotMusicView">
        <header>
            <img src="../../../../public/img/.png">
            <p> :{{ date.MM < 10 ? '0' + date.MM : date.MM }}{{ date.DD < 10 ? '0' + date.DD : date.DD }} </p>
        </header>
        <div class="content">
            <div class="loading" v-show="loadingIsShow">
                <img src="../../../../public/img/.gif">
            </div>
            <section class="songsList">
                <SongItemComp v-for="(item, index) in songList" :key="index" :songItem="item" :index="index"
                    :songList="songList"></SongItemComp>
            </section>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import SongItemComp from '@/components/SongItemComp.vue';
export default {
    name: 'HotMusicView',
    data: function () {
        return {
            loadingIsShow: false,
            songList: [],
            date: {
                MM: new Date().getMonth() + 1,
                DD: new Date().getDate()
            }
        }
    },
    components: {
        SongItemComp
    },
    created: function () {
        this.loadingIsShow = true;
        axios.get('./data/hotMusic.json')
            .then((data) => {
                this.loadingIsShow = false;
                let tracks = data.data.playlist.tracks;
                tracks.forEach((v) => {
                    let prevName = [];

                    v.ar.forEach((v) => {
                        prevName.push(v.name);
                    });
                    prevName = prevName.join('/');
                    this.songList.push({
                        id: v.id,
                        // URL
                        URL: `https://music.163.com/song/media/outer/url?id=${v.id}.mp3`,
                        // (xxx)
                        totalTime: parseInt(v.dt / 1000),
                        // 
                        coverURL: v.al.picUrl,
                        // 
                        songName: v.name,
                        // 
                        prevName: prevName,
                        // 
                        backName: v.al.name,
                        // 
                        sqMusic: v.sq
                    });
                });
            });
    }
}
</script>

<style lang="less">
.HotMusicView {
    >header {
        background-image: url('../../../../public/img/hot_music_bg_2x.jpg');
        background-size: cover;
        background-repeat: no-repeat;
        padding: 25rem;

        >img {
            width: 40%;
            margin-bottom: 5rem;
        }

        >p {
            color: #fff;
            font-size: 14rem;
            font-weight: bold;
        }
    }

    >.content {
        >.loading {
            padding: 50rem 0;

            >img {
                display: block;
                width: 50rem;
                margin: 0 auto;
            }
        }
    }
}
</style>