<template>
    <div class="RecommendView">
        <!--  -->
        <section class="bianji">
            <header>
                <span></span>
            </header>
            <div class="content">
                <RecommendSongsItemComp v-for="(item, index) in RecommendSongs" :key="index" :RecommendSongsItem="item">
                </RecommendSongsItemComp>
            </div>
        </section>
        <!-- -->
        <section class="newMusic">
            <header>
                <span></span>
            </header>
            <section class="songsList">
                <SongItemComp v-for="(item, index) in songList" :key="index" :songItem="item" :index="index"
                    :songList="songList"></SongItemComp>
            </section>
        </section>
    </div>
</template>
  
<script>
import axios from 'axios';
import RecommendSongsItemComp from '@/components/RecommendSongsItemComp.vue';
import SongItemComp from '@/components/SongItemComp.vue';
export default {
    name: 'RecommendView',
    data: function () {
        return {
            RecommendSongs: [],
            songList: []
        }
    },
    components: {
        RecommendSongsItemComp,
        SongItemComp
    },
    created: function () {
        axios.get('https://apis.netstart.cn/music/personalized?limit=6')
            .then((data) => {
                data.data.result.forEach((v) => {
                    this.RecommendSongs.push({
                        playCount: parseInt(v.playCount / 10000),
                        picUrl: v.picUrl,
                        name: v.name,
                        id: v.id
                    });
                });
                return axios.get('https://apis.netstart.cn/music/personalized/newsong', { params: { limit: 20 } });
            })
            .then(((data) => {
                data.data.result.forEach((v) => {
                    let prevName = [];
                    v.song.album.artists.forEach((v) => {
                        prevName.push(v.name);
                    });
                    prevName = prevName.join('/');

                    this.songList.push({
                        id: v.id,
                        // URL
                        URL: `https://music.163.com/song/media/outer/url?id=${v.id}.mp3`,
                        // (xxx)
                        totalTime: parseInt(v.song.duration / 1000),
                        // 
                        coverURL: v.picUrl,
                        // 
                        songName: v.song.name,
                        // 
                        prevName: prevName,
                        // 
                        backName: v.song.album.name,
                        // 
                        sqMusic: v.song.sqMusic
                    });
                });
            }));
    }
}
</script>
  
<style lang="less">
.RecommendView {

    >.bianji,
    >.newMusic {
        >header {
            padding: 20rem 0;

            >span {
                font-size: 16rem;
                border-left: 4px solid #d43c33;
                padding-left: 10rem;
            }
        }
    }

    >.bianji {
        >.content {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }
    }
}
</style>
  