<script>
import axios from 'axios';
import lodash from 'lodash';
import SearchResult from '@/components/SearchResult.vue';
export default {
    name: "SearchView",
    components: { SearchResult },
    data: function () {
        return {
            // 
            hotResult: [],
            // 
            inputWord: '',
            // 
            serchList: [],
            loadingIsShow: false,
            // ,,
            chooseSearchItem: ''
        }
    },
    computed: {
        searchHistory: {
            get: function () {
                return this.$store.state.searchHistory;
            },
            set: function (value) {
                this.$store.commit({ type: 'setSearchHistory', value: value });
                localStorage.setItem('searchHistory', JSON.stringify(value));
            }
        }
    },
    created: function () {
        axios.get('https://apis.netstart.cn/music/search/hot').then((data) => {
            this.hotResult = data.data.result.hots;
        });
    },
    watch: {
        inputWord: {
            handler: lodash.debounce(function (newValue) {
                this.isShowSearchResult = false;
                if (newValue == '') {
                    this.serchList = [];
                } else {
                    this.loadingIsShow = true;
                    this.serchList = [];
                    axios.get('https://apis.netstart.cn/music/search/suggest', { params: { keywords: newValue, type: 'mobile' } })
                        .then((data) => {
                            if (data.data.result) {
                                this.serchList = data.data.result.allMatch;
                                this.loadingIsShow = false;
                            }
                        });
                }
            }, 500)
        }
    },
    methods: {
        // 
        addHistory: function (historyItem) {
            let searchHistory = lodash.cloneDeep(this.searchHistory);
            if (searchHistory.includes(historyItem)) {
                let index = searchHistory.indexOf(historyItem);
                searchHistory.splice(index, 1);
                searchHistory.unshift(historyItem);
            } else {
                searchHistory.unshift(historyItem);
            }
            this.searchHistory = searchHistory;
        },
        cancleIconClick: function (index) {
            let searchHistory = lodash.cloneDeep(this.searchHistory);
            searchHistory.splice(index, 1);
            this.searchHistory = searchHistory;
        },
        keyupMethed: function () {
            if (this.inputWord != '') {
                this.chooseSearchItem = this.inputWord;
                this.addHistory(this.inputWord);
            }
            let searchInput = document.querySelector('.SearchView>header>.searchInput');
            searchInput.blur();
        },
        focusMethod: function () {
            this.chooseSearchItem = '';
        },
        historyBtnClick: function (historyWord) {
            this.inputWord = historyWord;
            this.chooseSearchItem = historyWord;
            this.addHistory(this.inputWord);
        },
        serchListClick: function (item) {
            this.inputWord = item;
            this.chooseSearchItem = item;
            this.addHistory(this.inputWord);
        },
        svgClick: function () {
            this.inputWord = '';
            this.chooseSearchItem = '';
        }
    }
}
</script>

<style lang="less">
.SearchView {
    >header {
        position: relative;
        padding: 20rem 0;
        border-bottom: 1rem solid gray;

        >.searchInput {
            display: block;
            width: 90%;
            height: 30rem;
            margin: 0 auto;
            border-radius: 20rem;
            background-color: rgb(235, 236, 236);
            border: none;
            outline: none;
            text-indent: 35rem;
        }

        >.searchIcon,
        >.cancelIcon {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 15rem;
            height: $width;

        }

        >.searchIcon {
            left: 30rem;
        }

        >.cancelIcon {
            right: 30rem;
        }
    }

    >.hotSeachr {
        .title {
            padding: 15rem 20rem;
            font-size: 10rem;
        }

        .hotwords {
            display: flex;
            flex-wrap: wrap;
            padding: 0 20rem;

            .item {
                border: 0.5rem solid gray;
                border-radius: 20rem;
                padding: 5rem 10rem;
                margin: 0 20rem 5rem 0;
            }
        }

        .history {

            >p {
                font-size: 16rem;
                display: flex;
                align-items: center;
                justify-content: space-evenly;

                >span {
                    width: 80%;
                    border-bottom: 1rem rgba(128, 128, 128, 0.458) solid;
                    padding: 15rem 0;
                }

                .clockIcon,
                .cancleIcon {
                    width: 15rem;
                    height: $width;
                }
            }

        }
    }

    >.suggestList {
        p {
            padding: 20rem;
            border-bottom: 1rem solid gray;
            color: #507daf;
            font-size: 14rem;
        }

        >.loading {
            padding: 50rem 0;

            >img {
                display: block;
                width: 50rem;
                margin: 0 auto;
            }
        }

        .list {
            >li {
                padding: 15rem;
                border-bottom: 0.3rem solid rgba(128, 128, 128, 0.474);
                display: flex;
                align-items: center;
                font-size: 14rem;

                >.searchIcon {
                    width: 14rem;
                    height: $width;
                    margin-right: 10rem;
                }
            }

        }
    }
}
</style>