<template>
    <div class="SongView">
        <div class="content">
            <!--  -->
            <img src="../../../../public/img/needle-ab.png"
                v-bind:class="{ headSet: true, noodelRun: playingMusicObj.isPlay, noodelPau: !playingMusicObj.isPlay }">
            <div :class="{ cilcle: true, roundAni: true, running: playingMusicObj.isPlay, paused: !playingMusicObj.isPlay }"
                @click="SongViewClick">
                <div class="round"></div>
                <img :src="playingSong.coverURL">
            </div>
        </div>
    </div>
</template>

<script>
import lodash from 'lodash';
export default {
    name: 'SongView',
    methods: {
        SongViewClick: function () {
            this.$router.push({ path: '/palyPage/lyric' });
        }
    },
    computed: {
        playingMusicObj: {
            get: function () {
                return this.$store.state.playingMusicObj;
            },
            set(value) {
                this.$store.commit({ type: 'setPlayingMusicObj', value: value });
                localStorage.setItem('playingMusicObj', JSON.stringify(value));
            }
        },
        playingSong: {
            get: function () {
                return this.playingMusicObj.playingSong;
            },
            set: function (value) {
                let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
                playingMusicObj.playingSong = value;
                this.playingMusicObj = playingMusicObj;
                localStorage.setItem('playingMusicObj', JSON.stringify(this.playingMusicObj));
            }
        },
    }
}
</script>

<style lang="less">
// 
@keyframes roundAni {
    0% {
        transform: rotateZ(0deg);
    }

    100% {
        transform: rotateZ(360deg);
    }
}

// ()
@keyframes noodelAniRun {
    0% {
        transform: rotateZ(315deg);
    }

    100% {
        transform: rotateZ(360deg);
    }
}

// ()
@keyframes noodelAniPau {
    0% {
        transform: rotateZ(360deg);
    }

    100% {
        transform: rotateZ(315deg);
    }
}

.SongView {
    overflow: hidden;
    height: 80vh;

    >.content {
        position: relative;
        height: 100%;

        // 
        .roundAni {
            animation: roundAni;
            animation-duration: 10s;
            animation-iteration-count: infinite;
            animation-timing-function: linear;
        }

        // 
        .running {
            animation-play-state: running;
        }

        // 
        .paused {
            animation-play-state: paused;
        }

        .headSet {
            display: block;
            width: 120rem;
            position: absolute;
            top: 0rem;
            z-index: 1;
            left: 40%;
            transform-origin: 16rem 16rem;
            transform: rotateZ(315deg);
        }

        .noodelRun {
            animation: noodelAniRun;
            animation-timing-function: linear;
            animation-iteration-count: 1;
            animation-duration: 1s;
            transform: rotateZ(360deg);
        }

        .noodelPau {
            animation: noodelAniPau;
            animation-timing-function: linear;
            animation-iteration-count: 1;
            animation-duration: 1s;
            transform: rotateZ(315deg);
        }

        .cilcle {
            width: 80vw;
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            top: 130rem;
            left: calc(50% - 40vw);

            .round {
                width: 100%;
                height: 80vw;
                background-image: url('../../../../public/img/.png');
                background-repeat: no-repeat;
                background-size: cover;
            }

            >img {
                position: absolute;
                display: block;
                width: 65%;
                border-radius: 50%;
            }
        }
    }
}
</style>