<template>
    <div class="RecommendSongsItemComp" @click="itemClick()">
        <div class="top">
            <span><img src="../../public/img/24gl-headset.png">{{ RecommendSongsItem.playCount }}</span>
            <img :src="RecommendSongsItem.picUrl">
        </div>
        <p class="title">{{ RecommendSongsItem.name }}</p>
    </div>
</template>

<script>
export default {
    name: "RecommendSongsItemComp",
    props: { RecommendSongsItem: Object },
    methods: {
        itemClick: function () {
            this.$router.push({ path: '/songList', query: { id: this.RecommendSongsItem.id } });
        }
    }
}
</script>

<style lang="less">
.RecommendSongsItemComp {
    width: 32.5%;
    margin: 5rem 0;

    >.top {
        position: relative;

        >span {
            position: absolute;
            top: 0;
            right: 10rem;
            color: #fff;
            display: flex;
            align-items: center;

            img {
                width: 15rem;
            }
        }

        >img {
            width: 100%;
            height: $width;
        }
    }

    >.title {
        text-align: center;
        font-size: 12rem;
    }
}
</style>