<template>
    <div :class="{ PlayingListItemComponent: true, active: PlayingListItem.id == playingSong.id }">
        <p class="songName" @click="itemClick(PlayingListItem)">
            <img v-show="PlayingListItem.id == playingSong.id" src="../../public/img/.gif">
            <span class="prevName">{{ PlayingListItem.songName }}</span>
            <span> - </span>
            <span class="backName">{{ PlayingListItem.prevName }}</span>
        </p>
        <svg t="1665109310662" @click="svgClick(index)" class="delIcon" viewBox="0 0 1024 1024" version="1.1"
            xmlns="http://www.w3.org/2000/svg" p-id="2956" width="128" height="128">
            <path
                d="M842.947458 778.116917 576.847937 512.013303 842.946434 245.883083c8.67559-8.674567 13.447267-20.208251 13.43908-32.477692-0.008186-12.232602-4.7727-23.715121-13.414521-32.332383-8.655124-8.677637-20.149922-13.450337-32.384571-13.4575-12.286838 0-23.808242 4.771677-32.474622 13.434987L512.019443 447.143876 245.88206 181.050496c-8.66331-8.66331-20.175505-13.434987-32.416294-13.434987-12.239765 0-23.75196 4.770653-32.414247 13.43294-8.66024 8.636704-13.428847 20.12434-13.437034 32.356942-0.008186 12.269441 4.76349 23.803125 13.437034 32.476669l266.135336 266.13022L181.050496 778.11794c-8.664334 8.66331-13.43601 20.173458-13.43601 32.41527 0 12.239765 4.7727 23.752983 13.437034 32.417317 8.662287 8.66331 20.173458 13.43294 32.413224 13.43294 12.240789 0 23.754007-4.770653 32.416294-13.43294l266.134313-266.100544 266.101567 266.100544c8.66331 8.66331 20.185738 13.43294 32.4429 13.43294 12.265348-0.008186 23.74889-4.771677 32.369222-13.412474C860.81643 825.081555 860.821547 795.991006 842.947458 778.116917z"
                p-id="2957" fill="#2c2c2c"></path>
        </svg>
    </div>
</template>

<script>
import lodash from 'lodash';
export default {
    name: 'PlayingListItemComponent',
    props: {
        PlayingListItem: Object,
        index: Number
    },
    computed: {
        playingSong: {
            get: function () {
                return this.playingMusicObj.playingSong;
            },
            set: function (value) {
                let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
                playingMusicObj.playingSong = value;
                this.playingMusicObj = playingMusicObj;
            }
        },
        playingMusicObj: {
            get: function () {
                return this.$store.state.playingMusicObj;
            },
            set(value) {
                this.$store.commit({ type: 'setPlayingMusicObj', value: value });
                localStorage.setItem('playingMusicObj', JSON.stringify(value));
            }
        },
        playingSongList: {
            get: function () {
                return this.$store.state.playingSongList;
            },
            set: function (value) {
                this.$store.commit({ type: 'setPlayingSongList', value: value });
                localStorage.setItem('playingSongList', JSON.stringify(value));
            }
        }
    },
    methods: {
        itemClick: function (song) {
            let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
            playingMusicObj.isPlay = true;
            this.playingMusicObj = playingMusicObj;
            let songList = this.playingSongList;
            songList.forEach((v) => {
                if (v.id == song.id) {
                    this.playingSong = v;
                }
            });
            let audio = document.querySelector('#app>.musicAudio');
            setTimeout(() => {
                audio.play();
            }, 500);
        },
        svgClick: function (index) {
            let audio = document.querySelector('#app>.musicAudio');
            let playingSongList = lodash.cloneDeep(this.playingSongList);
            if (playingSongList[index].id == this.playingSong.id) {
                audio.pause();
                let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
                playingMusicObj.isPlay = false;
                this.playingMusicObj = playingMusicObj;
            }
            playingSongList.splice(index, 1);
            this.playingSongList = playingSongList;
        }
    }
};
</script>

<style lang="less">
.PlayingListItemComponent {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10rem 0;

    >.delIcon {
        width: 5vw;
        height: $width;
    }

    >.songName {
        font-size: 14rem;
        width: 90%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        >img {
            width: 20rem;
            margin-right: 5rem;
        }
    }
}

.active {
    >.songName {
        font-size: 14rem;
        color: red;
    }
}
</style>