<template>
    <div class="songListView">
        <header v-bind:style="{ 'backgroundImage': backgroundImage }">
            <div class="left">
                <span class="icon"></span>
                <span><img src="../../public/img/24gl-headset.png">{{ parseInt(headerData.playCount / 10000) }}
                    </span>
                <img v-bind:src="headerData.coverImgUrl">
            </div>
            <div class="right">
                <p class="title">{{ headerData.name }}</p>
                <div class="arthor">
                    <img v-bind:src="headerData.avatarUrl">
                    <p class="name">{{ headerData.nickname }}</p>
                </div>
                <div class="PingLun" @click="PingLunClick()">
                    <svg t="1667288741101" class="icon" viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="2323" width="128" height="128">
                        <path
                            d="M853.333333 768c35.413333 0 64-20.650667 64-55.978667V170.581333A63.978667 63.978667 0 0 0 853.333333 106.666667H170.666667C135.253333 106.666667 106.666667 135.253333 106.666667 170.581333v541.44C106.666667 747.285333 135.338667 768 170.666667 768h201.173333l110.016 117.44a42.752 42.752 0 0 0 60.586667 0.042667L651.904 768H853.333333z m-219.029333-42.666667h-6.250667l-115.797333 129.962667c-0.106667 0.106667-116.010667-129.962667-116.010667-129.962667H170.666667c-11.776 0-21.333333-1.621333-21.333334-13.312V170.581333A21.205333 21.205333 0 0 1 170.666667 149.333333h682.666666c11.776 0 21.333333 9.536 21.333334 21.248v541.44c0 11.754667-9.472 13.312-21.333334 13.312H634.304zM341.333333 490.666667a42.666667 42.666667 0 1 0 0-85.333334 42.666667 42.666667 0 0 0 0 85.333334z m170.666667 0a42.666667 42.666667 0 1 0 0-85.333334 42.666667 42.666667 0 0 0 0 85.333334z m170.666667 0a42.666667 42.666667 0 1 0 0-85.333334 42.666667 42.666667 0 0 0 0 85.333334z"
                            fill="#ffffff" p-id="2324"></path>
                    </svg>
                    <span></span>
                </div>
            </div>
        </header>
        <section class="songsList">
            <p class="title"></p>
        </section>
        <van-list class="songList" v-model="loading" :finished="finished" finished-text="" @load="onLoad">
            <SongItemComp v-for="(item, index) in songList" :key="index" :songItem="item" :index="index"
                :songList="songList"></SongItemComp>
        </van-list>
    </div>
</template>

<script>
import axios from 'axios';
import SongItemComp from '@/components/SongItemComp.vue';
export default {
    name: 'songListView',
    data: function () {
        return {
            routeURL: '/palyPage',
            fromData: null,
            headerData: {
                coverImgUrl: undefined,
                playCount: 0,
                name: undefined,
                avatarUrl: undefined,
                nickname: undefined
            },
            songList: [],
            // van-list
            offset: 0,
            loading: false,
            finished: false,
        }
    },
    methods: {
        onLoad() {
            ++this.offset;
            axios.get('https://apis.netstart.cn/music/playlist/track/all', {
                params: {
                    id: this.fromData.id,
                    limit: 10,
                    offset: this.offset * 10
                }
            })
                .then((data) => {
                    let songs = data.data.songs;
                    songs.forEach((v) => {
                        let ars = v.ar;
                        let prevName = [];
                        ars.forEach((v) => {
                            prevName.push(v.name);
                        });
                        prevName = prevName.join('/');
                        this.songList.push({
                            id: v.id,
                            // URL
                            URL: `https://music.163.com/song/media/outer/url?id=${v.id}.mp3`,
                            // (xxx)
                            totalTime: parseInt(v.dt / 1000),
                            // 
                            coverURL: v.al.picUrl,
                            // 
                            songName: v.name,
                            // 
                            prevName: prevName,
                            // 
                            backName: v.al.name,
                            // 
                            sqMusic: v.sq
                        });
                    });

                    // 
                    this.loading = false;

                    // 
                    if (songs.length == 0) {
                        this.finished = true;
                    }
                });
        },
        PingLunClick: function () {
            this.$router.push({ path: '/comments', query: { commentType: 'playlist', id: this.fromData.id } });
        }
    },
    components: { SongItemComp },
    created: function () {
        this.fromData = this.$route.query;
        axios.get('https://apis.netstart.cn/music/playlist/detail', { params: { id: this.fromData.id } })
            .then((data) => {
                let playlist = data.data.playlist;
                let creator = playlist.creator;
                this.headerData = {
                    coverImgUrl: playlist.coverImgUrl,
                    playCount: playlist.playCount,
                    name: playlist.name,
                    avatarUrl: creator.avatarUrl,
                    nickname: creator.nickname
                };
                return axios.get('https://apis.netstart.cn/music/playlist/track/all', {
                    params: {
                        id: this.fromData.id,
                        limit: 10,
                        offset: this.offset * 10
                    }
                })
            })
            .then((data) => {
                let songs = data.data.songs;
                songs.forEach((v) => {
                    let ars = v.ar;
                    let prevName = [];
                    ars.forEach((v) => {
                        prevName.push(v.name);
                    });
                    prevName = prevName.join('/');
                    this.songList.push({
                        id: v.id,
                        // URL
                        URL: `https://music.163.com/song/media/outer/url?id=${v.id}.mp3`,
                        // (xxx)
                        totalTime: parseInt(v.dt / 1000),
                        // 
                        coverURL: v.al.picUrl,
                        // 
                        songName: v.name,
                        // 
                        prevName: prevName,
                        // 
                        backName: v.al.name,
                        // 
                        sqMusic: v.sq
                    });
                });
            });
    },
    computed: {
        backgroundImage: {
            get: function () {
                let coverImgUrl = this.headerData.coverImgUrl;
                return coverImgUrl ? `url(${coverImgUrl})` : '';
            }
        }
    }

}
</script>

<style lang="less">
.songListView {
    >header {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 20rem 0;

        >.left {
            width: 126rem;
            position: relative;

            >img {
                display: block;
                width: 100%;
            }

            >.icon {
                position: absolute;
                top: 10rem;
                left: 0;
                padding: 0.5rem 6rem;
                background-color: red;
                color: #fff;
                border-radius: 0 35% 35% 0;
            }

            >span:nth-child(2) {
                display: flex;
                position: absolute;
                top: 0;
                right: 0;
                color: #fff;

                >img {
                    display: block;
                    width: 15rem;
                }
            }
        }

        >.right {
            width: 50%;
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            >.title {
                font-size: 18rem;
                margin-bottom: 10rem;
                color: #fff;
            }

            >.arthor {
                display: flex;
                align-items: center;

                >img {
                    display: block;
                    width: 30rem;
                    border-radius: 50%;
                    margin-right: 10rem;
                }

                >.name {
                    font-size: 14rem;
                    font-weight: 500;
                    color: gray;
                }
            }

            >.PingLun {
                width: 70rem;
                color: #fff;
                font-weight: bold;
                display: flex;
                align-items: center;
                margin: 10rem 0;
                background-color: rgba(241, 243, 244, 0.532);
                border-radius: 20rem;
                justify-content: center;
                padding: 5rem 0;

                >.icon {
                    width: 20rem;
                    height: 20rem;
                    margin-right: 5rem;
                }
            }
        }
    }

    >.songsList {
        .title {
            background-color: #eeeff0;
            padding: 5rem;
        }
    }
}
</style>