<template>
    <div class="LyricView" @click.self="LyricViewClick">
        <div class="line" v-show="isShowLine">
            <span>{{ lookingTime }}</span>
            <svg t="1668129382943" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
                p-id="2303" width="128" height="128">
                <path
                    d="M512 0C230.4 0 0 230.4 0 512s230.4 512 512 512 512-230.4 512-512S793.6 0 512 0z m0 981.333333C253.866667 981.333333 42.666667 770.133333 42.666667 512S253.866667 42.666667 512 42.666667s469.333333 211.2 469.333333 469.333333-211.2 469.333333-469.333333 469.333333z"
                    p-id="2304" fill="#ffffff"></path>
                <path
                    d="M672 441.6l-170.666667-113.066667c-57.6-38.4-106.666667-12.8-106.666666 57.6v256c0 70.4 46.933333 96 106.666666 57.6l170.666667-113.066666c57.6-42.666667 57.6-106.666667 0-145.066667z"
                    p-id="2305" fill="#ffffff"></path>
            </svg>
        </div>
        <section class="lyric" @touchmove="lyricTouchmove" @touchend="lyricTouchend">
            <p v-for="(item, index) in lyric" :key="index" :class="{ active: index == getPlayingLyricIndex }">
                {{ item.lyric }}
            </p>
        </section>
    </div>
</template>

<script>
import lodash from 'lodash';
import axios from 'axios';
export default {
    name: 'LyricView',
    data: function () {
        return {
            lyric: [],
            isShowLine: false,
            lookingLyricObj: { time: 0 }
        }
    },
    computed: {
        playingMusicObj: {
            get: function () {
                return this.$store.state.playingMusicObj;
            },
            set(value) {
                this.$store.commit({ type: 'setPlayingMusicObj', value: value });
                localStorage.setItem('playingMusicObj', JSON.stringify(value));
            }
        },
        playingSong: {
            get: function () {
                return this.playingMusicObj.playingSong;
            },
            set: function (value) {
                let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
                playingMusicObj.playingSong = value;
                this.playingMusicObj = playingMusicObj;
                localStorage.setItem('playingMusicObj', JSON.stringify(this.playingMusicObj));
            }
        },
        getPlayingLyricIndex: {
            get: function () {
                let playing = this.playingMusicObj.playing;
                let lyric = this.lyric;
                let index;
                lyric.forEach((v, i, a) => {
                    if (i != a.length - 1) {
                        let nextIndex = i + 1;
                        if (playing >= v.time && playing < a[nextIndex].time) {
                            index = i;
                        }
                    } else {
                        let totalTime = this.playingSong.totalTime;
                        if (playing >= v.time && playing <= totalTime) {
                            index = i;
                        }
                    }
                });
                return index;
            }
        },
        lookingTime: {
            get: function () {
                let time = this.lookingLyricObj.time;
                let mm = parseInt(time / 60) < 10 ? '0' + parseInt(time / 60) : parseInt(time / 60);
                let ss = parseInt(time % 60) < 10 ? '0' + parseInt(time % 60) : parseInt(time % 60);
                return mm + ':' + ss;
            }
        }
    },
    methods: {
        LyricViewClick: function () {
            this.$router.go(-1);
        },
        lyricTouchmove: function () {
            this.isShowLine = true;
            let lyricDOM = document.querySelector('.LyricView>.lyric');
            // 
            let lyricHeight = lyricDOM.scrollHeight - lyricDOM.offsetHeight;
            let lyricNum = this.lyric.length;
            let lyricOffset = lyricHeight / lyricNum;
            let pHeight = document.querySelectorAll('.lyric>p')[0].offsetHeight;
            let playLricIndex = Math.round((lyricDOM.scrollTop + (pHeight / 2)) / lyricOffset - 1);
            let lyricObj = this.lyric[playLricIndex];
            if (lyricObj) {
                if (!isNaN(lyricObj.time)) {
                    this.lookingLyricObj = lodash.cloneDeep(lyricObj);
                }
            }
        },
        lyricTouchend: function () {
            let audio = document.querySelector('#app>.musicAudio');
            if (this.lookingLyricObj) {
                if (!isNaN(this.lookingLyricObj.time)) {
                    audio.currentTime = this.lookingLyricObj.time;
                }
            }
            this.isShowLine = false;
        }
    },
    created: function () {
        // https://apis.netstart.cn/music/lyric
        axios.get('https://apis.netstart.cn/music/lyric', { params: { id: this.playingSong.id } })
            .then((data) => {
                let lyric = data.data.lrc.lyric;
                lyric.split(/\n/g).forEach((v) => {
                    let indexOfL = v.indexOf('[');
                    let indexOfR = v.indexOf(']');
                    // 
                    let time = v.slice(indexOfL + 1, indexOfR);
                    let dotIndex = time.indexOf('.');
                    time = time.slice(0, dotIndex);
                    let mm = parseInt(time.slice(0, 2));
                    let ss = parseInt(time.slice(3));

                    let lyric = v.slice(indexOfR + 1);
                    this.lyric.push({ time: mm * 60 + ss, lyric: lyric });
                });
            });
    },
    watch: {
        'playingSong.id': {
            handler: function (newValue) {
                this.lyric = [];
                axios.get('https://apis.netstart.cn/music/lyric', { params: { id: newValue } })
                    .then((data) => {
                        let lyric = data.data.lrc.lyric;
                        lyric.split(/\n/g).forEach((v) => {
                            let indexOfL = v.indexOf('[');
                            let indexOfR = v.indexOf(']');
                            // 
                            let time = v.slice(indexOfL + 1, indexOfR);
                            let dotIndex = time.indexOf('.');
                            time = time.slice(0, dotIndex);
                            let mm = parseInt(time.slice(0, 2));
                            let ss = parseInt(time.slice(3));

                            let lyric = v.slice(indexOfR + 1);
                            this.lyric.push({ time: mm * 60 + ss, lyric: lyric });
                        });
                    });
            }
        },
        'getPlayingLyricIndex': {
            handler: function (newValue) {
                let lyricDOM = document.querySelector('.LyricView>.lyric');
                // 
                let lyricHeight = lyricDOM.scrollHeight - lyricDOM.offsetHeight;
                let lyricNum = this.lyric.length;
                let lyricOffset = lyricHeight / lyricNum;
                // newValue = (lyricDOM.scrollTop + (pHeight / 2))/ lyricOffset - 1 
                lyricDOM.scrollTop = (newValue + 1) * lyricOffset;
            }
        }
    }
}
</script>

<style lang="less">
.LyricView {
    height: 80vh;
    position: relative;

    >.line {
        width: 75%;
        height: 1px;
        background-color: #fff;
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        >span,
        >.icon {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
        }

        >span {
            width: 31rem;
            text-align: center;
            left: -35rem;
            color: #fff;
            font-size: 12px;
        }

        >.icon {
            right: -25rem;
            width: 20rem;
            height: 20rem;
        }
    }

    >.lyric {
        width: 75%;
        height: 90%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        overflow: auto;

        &::before,
        &::after {
            display: block;
            content: "";
            width: 100%;
            height: 50%;
        }

        &::-webkit-scrollbar {
            display: none;
        }

        >p {
            text-align: center;
            color: rgba(255, 255, 255, 0.601);
            font-size: 16rem;
            margin: 5rem 0;
            height: 20rem;
            line-height: 20rem;
            width: 100%;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        .active {
            color: #fff;
        }
    }
}
</style>