<template>
    <div class="SongItemComp" @click="liClick()">
        <span class="num">{{ index + 1 }}</span>
        <div class="content">
            <div class="center">
                <p class="songName">{{ songItem.songName }}</p>
                <p class="buttom">
                    <img src="../../public/img/SQ.png" class="SQIcon" v-if="songItem.sqMusic != null">
                    <span class="prevName">{{ songItem.prevName }}</span> - <span class="backName">{{ songItem.backName
                    }}</span>
                </p>
            </div>
            <div class="right">
                <div class="btn">
                    <svg t="1663934743117" v-if="playingMusicObj.playingSong.id != songItem.id" class="icon"
                        viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3155" width="128"
                        height="128">
                        <path
                            d="M512 42.666667C252.793333 42.666667 42.666667 252.793333 42.666667 512s210.126667 469.333333 469.333333 469.333333 469.333333-210.126667 469.333333-469.333333S771.206667 42.666667 512 42.666667z m0 896c-235.64 0-426.666667-191.026667-426.666667-426.666667s191.026667-426.666667 426.666667-426.666667 426.666667 191.026667 426.666667 426.666667-191.026667 426.666667-426.666667 426.666667z m-74.666667-213.38a53.373333 53.373333 0 0 1-53.333333-53.333334V352.066667A53.333333 53.333333 0 0 1 467.373333 308L702 467.933333a53.333333 53.333333 0 0 1 0 88.133334L467.373333 716a53.286667 53.286667 0 0 1-30.04 9.286667z m0.08-383.933334a11.093333 11.093333 0 0 0-5.08 1.28 10.446667 10.446667 0 0 0-5.666666 9.433334v319.866666a10.666667 10.666667 0 0 0 16.666666 8.82l234.666667-159.94a10.666667 10.666667 0 0 0 0-17.626666l-234.666667-159.933334a10.313333 10.313333 0 0 0-5.906666-1.92z"
                            fill="#8a8a8a" p-id="3156"></path>
                    </svg>
                    <img v-if="playingMusicObj.playingSong.id == songItem.id" src="../../public/img/.gif">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import lodash from 'lodash';
export default {
    name: 'SongItemComp',
    props: {
        index: Number,
        songList: Array,
        songItem: Object
    },
    computed: {
        playingMusicObj: {
            get: function () {
                return this.$store.state.playingMusicObj;
            },
            set(value) {
                this.$store.commit({ type: 'setPlayingMusicObj', value: value });
                localStorage.setItem('playingMusicObj', JSON.stringify(value));
            }
        },
        playingSongList: {
            get: function () {
                return this.$store.state.playingSongList;
            },
            set: function (value) {
                this.$store.commit({ type: 'setPlayingSongList', value: value });
                localStorage.setItem('playingSongList', JSON.stringify(value));
            }
        }
    },
    methods: {
        liClick: function () {
            let songList = lodash.cloneDeep(this.songList);

            let playingMusicObj = lodash.cloneDeep(this.playingMusicObj);
            playingMusicObj.playingSong = this.songItem;
            playingMusicObj.songList = songList;
            playingMusicObj.isPlay = true;
            this.playingMusicObj = playingMusicObj;

            this.playingSongList = songList;

            let audio = document.querySelector('#app>.musicAudio');
            if (playingMusicObj.isPlay) {
                setTimeout(() => {
                    audio.play();
                }, 500);
            } else {
                audio.pause();
            }
        }
    }
}
</script>

<style lang="less">
.SongItemComp {
    display: flex;
    align-items: center;
    box-sizing: border-box;

    .num {
        width: 10%;
        text-align: center;
        font-size: 14rem;
        color: gray;
        border-bottom: 1rem solid white;
    }

    .content {
        width: 90%;
        display: flex;
        align-items: center;
        border-bottom: 1rem solid gray;

        .center {
            width: 80%;
            padding: 10rem 0;

            >.songName,
            >.buttom {
                width: 100%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }

            .songName {
                font-size: 18rem;
                margin-bottom: 5rem;
            }

            .buttom {
                font-weight: 100;
                color: gray;

                >.SQIcon {
                    width: 12rem;
                    margin-right: 5rem;
                }
            }
        }

        .right {
            width: 20%;
            height: 100%;
            text-align: center;

            >.btn {
                .icon {
                    width: 40%;
                    height: $width;
                }

                >img {
                    width: 40%;
                    height: $width;
                }
            }

        }
    }
}
</style>